const MistOverlay = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	display: ${(props) => (props.doAnimate ? "block" : "none")};
	width: 100%;
	height: 100vh;
	max-height: 100vh;
	overflow: hidden;
	z-index: 30;
	background: rgb(207, 207, 207);
	opacity: 1;
`;

const Mist = styled.div`
	position: absolute;
	background: ${(props) => 'url("./images/' + props.img + '")'};
	background-size: cover;
	animation: ${(props) =>
		props.img === "fog-high.png"
			? "animateUp 8s infinite ease"
			: "animateDown 8s infinite ease"};
	width: 100%;
	height: 100%;

	@keyframes animateUp {
		0% {
			transform: translate(0%, 0%) scale(1.25);
			opacity: 1;
		}
		100% {
			transform: translate(0%, -100%);
			opacity: 0;
		}
	}
	@keyframes animateDown {
		0% {
			transform: translate(0%, 0%) scale(1.25);
			opacity: 1;
		}
		100% {
			transform: translate(0%, 100%);
			opacity: 0;
		}
	}
`;

const Graphic = styled.div`
	position: relative;
	width: 100vw;
	height: 100vh;
	text-align: center;
	display: flex;
	justify-content: center;
	flex-wrap: nowrap;
	word-wrap: normal;
	word-break: normal;
	overflow-wrap: normal;
	align-items: center;
	font-family: "Mirza", cursive;
	animation: ${(props) => "intro " + props.duration + "s ease"};
	@keyframes intro {
		0% {
			opacity: 1;
			font-size: 1.5em;
			letter-spacing: 2px;
		}
		50% {
			opacity: 0.97;
		}
		100% {
			opacity: 0;
			font-size: 1.505em;
			letter-spacing: 3px;
		}
	}
`;

const CloseButton = styled.div`
	position: relative;
	font-size: 3em;
	height: 50px;
	color: #d64343;
	display: ${(props) => (props.display ? "block" : "none")};
	cursor: pointer;
	display: flex;
	justify-content: center;
	width: 100%;
	padding: 10px 0;

	@media only screen and (max-height: 432px) {
		height: auto;
		font-size: 2.3em;
		padding: 5px 0;
	}
`;

let realMapPosition = { x: 0, y: 0 };
let realMapScale = 1;
let itemStack = [];

function App() {
	// original dimensions of the Map image
	const originalW = 4048;
	const originalH = 3036;
	const originalRatio = originalW / originalH;

	const [image, setImage] = useState({
		w: window.innerWidth,
		h: window.innerHeight / originalRatio,
	});

	window.addEventListener("resize", () => {
		setImage({
			w: window.innerWidth,
			h: window.innerHeight / originalRatio,
		});
	});

	const [mapZoom, setMapZoom] = useState(1);
	const [mapPosition, setMapPosition] = useState({ x: 0, y: 0 });
	const [markerPos, setMarkerPos] = useState({ x: 0, y: 0 });
	const [spoilerLevel, setSpoilerLevel] = useState(Spoilers[0]);

	// const [realMapPosition, setRealMapPosition] = useState({ x: 0, y: 0 });
	const [mapItem, setMapItem] = useState(null);
	const [infoPanel, setInfoPanel] = useState(false);
	const [open, setOpen] = useState(true);

	const [doAnimate, setDoAnimate] = useState(false);
	const [introAnimation, setIntroAnimation] = useState(0);

	// allow mouse is set as false when the map is
	//  undergoing a transition (after clicking a map item)
	const [allowMouse, setAllowMouse] = useState(true);

	// set the focused map item.
	// calculate the position of the map and marker,
	//  and set the scale to the same as from getMapOffsetScale()
	const setFocusedItem = (item) => {
		// handling null map item
		if (item === null) {
			setMapZoom(realMapScale);
			setMapItem(null);
			return;
		}

		let x, y;

		// real coordinates of the map position and marker position
		const mX = parseFloat(item.position.x * parseFloat(image.w / originalW)),
			mY = parseFloat(item.position.y * parseFloat(image.h / originalH));

		// zoom in on the map slightly
		if (realMapScale < 2) {
			realMapScale = 2;
		}

		// set the map position x
		x = parseFloat(
			item.position.x * parseFloat(image.w / originalW) * realMapScale
		);
		x -= window.innerWidth / 2;
		// set the map position y
		y = parseFloat(
			item.position.y * parseFloat(image.h / originalH) * realMapScale
		);
		y -= window.innerHeight / 2;

		const imgW = image.w * realMapScale;
		const imgH = image.h * realMapScale;

		// keep the x and y within the map
		if (y < 0) {
			y = 0;
		}
		if (x < 0) {
			x = 0;
		}
		if (y + window.innerHeight > imgH) {
			y = imgH - window.innerHeight;
		}
		if (x + window.innerWidth > imgW) {
			x = imgW - window.innerWidth;
		}

		// console.log("Marker: \nx:" + mX + ", y:" + mY);
		// console.log("Map: \nx:" + x + ", y:" + y + "\n\n");

		setMapZoom(realMapScale);
		setMapPosition({ x: x, y: y });
		setMarkerPos({ x: mX, y: mY });
		setMapItem(item);
		// add to the previous items stack
		itemStack.push(item);

		// remove the hover cursor
		let el = document.getElementsByClassName("react-transform-element")[0];
		el.classList.remove("hover-cursor");
	};

	// when clicking a menu item, set Map zoom and selected item
	const handleMenuClick = (item) => {
		setIntroAnimation(introAnimation + 1);

		setDoAnimate(true);

		// set the focused item, which also sets the mapPosition X,Y
		setFocusedItem(item);
	};

	// function used with handleMapClick and handleMapItemHover
	//  to determine realMapPosition and realMapScale,
	//		via the react-transform-element styles,
	//		which includes the translate and scale options for transform
	const getMapOffsetScale = (e) => {
		// extract the react-transform-element style: translate(x, y) to get the PositionX,Y
		let transform = e.target.style.transform;
		// Get translate values
		let translate = transform
			.slice(transform.indexOf("(") + 1, transform.indexOf(")"))
			.replaceAll("px", "")
			.trim()
			.replaceAll(" ", "");

		// Get/set the scale
		let scale = transform
			.slice(transform.indexOf("scale(") + 6, transform.length)
			.replace(")", "")
			.trim();
		realMapScale = parseFloat(scale);

		let arr = translate.split(",");

		// Set the realMapPosition
		realMapPosition = {
			x: parseFloat(arr[0]) * -1,
			y: parseFloat(arr[1]) * -1,
		};
		if (isNaN(realMapPosition.x)) {
			realMapPosition.x = 0;
		}
		if (isNaN(realMapPosition.y)) {
			realMapPosition.y = 0;
		}
	};

	// function for clicking over map, check coordinates to see if a destination has been clicked
	const handleMapClick = (e) => {
		// first calculate the overflow offset of the Map PositionX,Y
		getMapOffsetScale(e);

		// get mouse coordinates within the map image
		const mouseX =
				parseFloat((e.clientX + realMapPosition.x) / realMapScale) *
				parseFloat(originalW / image.w),
			mouseY =
				parseFloat((e.clientY + realMapPosition.y) / realMapScale) *
				parseFloat(originalH / image.h);

		LegendData.forEach((item) => {
			item.menu &&
				item.menu.forEach((map_item) => {
					// DEBUGGING PURPOSES
					if (!map_item.bounds) {
						return;
					}

					if (
						mouseX >= map_item.bounds.x &&
						mouseY >= map_item.bounds.y &&
						mouseX <= map_item.bounds.x2 &&
						mouseY <= map_item.bounds.y2
					) {
						// add the transition to the map classlist,
						//  it is removed when panning in MapView
						e.target.classList.add("onclick-transition");

						// set the focus of the map to new item
						setFocusedItem(map_item);

						// disallow the mouse from being used until the onclick-transition has ended
						//  this is called in onTransitionEnded() in div.container
						setAllowMouse(false);
					}
				});
		});
	};

	const handleMapItemHover = (e) => {
		getMapOffsetScale(e);

		// convert mouse coordinates to full size map image
		const mouseX =
				parseFloat((e.clientX + realMapPosition.x) / realMapScale) *
				parseFloat(originalW / image.w),
			mouseY =
				parseFloat((e.clientY + realMapPosition.y) / realMapScale) *
				parseFloat(originalH / image.h);

		// hoveringAnItem is incremented if the user is hovering over a map item,
		//  and decremented otherwise
		let hoveringAnItem = 0;
		// activeItemCount is the count of all items which have a 'bounds' property
		let activeItemCount = 0;

		// therefore, we can check the difference between hoveringAnItem and activeItemCount
		//  to see if the user is currently hovering an item.
		//  this will let us add the '.hover-cursor' class afterwards.

		LegendData.forEach((item) => {
			item.menu &&
				item.menu.forEach((map_item) => {
					// DEBUGGING PURPOSES as some landmarks may not have 'bounds' yet {x,y,x2,y2}
					if (!map_item.bounds) {
						return;
					} else {
						activeItemCount++;
					}

					// increment hoveringAnItem if the cursor is over a mapItem
					if (
						mouseX >= map_item.bounds.x &&
						mouseY >= map_item.bounds.y &&
						mouseX <= map_item.bounds.x2 &&
						mouseY <= map_item.bounds.y2
					) {
						hoveringAnItem++;
					} else {
						hoveringAnItem--;
					}
				});
		});

		let cursorIsOverMapItem = -hoveringAnItem !== activeItemCount;
		// add or remove the hover-cursor class if the cursor is over the map item
		if (cursorIsOverMapItem) {
			e.target.classList.add("hover-cursor");
		} else {
			e.target.classList.remove("hover-cursor");
		}
	};

	// mist animation/quote on app load
	useEffect(() => {
		if (introAnimation === 0) {
			setDoAnimate(true);
			setIntroAnimation(introAnimation + 1);
		}
	});

	return (
		<div
			id="container"
			className="container"
			onMouseUp={(e) => {
				if (!allowMouse) {
					e.preventDefault();
					e.stopPropagation();
					return;
				}

				// only perform handleMapClick if the map wasn't being panned
				if (!e.target.classList.contains("dragging")) {
					if (e.target.classList[0] === "react-transform-element") {
						handleMapClick(e);
					}
				}
			}}
			onTouchStartCapture={(e) => {
				if (itemStack[itemStack.length - 1] !== "panned") {
					setAllowMouse(true);
				}

				// dissalow user from panning map until allowMouse is set as true (by onTransitionEnd!)
				if (!allowMouse) {
					e.preventDefault();
					e.stopPropagation();
					return;
				}
				// only perform handleMapClick if the map wasn't being panned
				if (!e.target.classList.contains("dragging")) {
					if (e.target.classList[0] === "react-transform-element") {
						handleMapClick(e);
					}
				}
			}}
			onMouseDown={(e) => {
				// if the last action is clicking a map item, we can setAllowMouse to true
				if (itemStack[itemStack.length - 1] !== "panned") {
					setAllowMouse(true);
				}

				// don't allow mouse to be clicked while the react-transform-element transition is happening
				if (!allowMouse) {
					e.preventDefault();
					e.stopPropagation();
					return;
				}
			}}
			onMouseMove={(e) => {
				// On mousemove, check if the cursor is over a map Item,
				//  if so, change the cursor to pointer to indicate that this can be clicked on
				if (!allowMouse) {
					return;
				}
				if (e.target.classList[0] === "react-transform-element") {
					handleMapItemHover(e);
				}
			}}
			onTransitionEnd={(e) => {
				if (e.elapsedTime === 1) {
					setAllowMouse(true);
				}
			}}
		>
			{/* On menuItemClick, change mapItem. Keep track of mapItem to set the active item */}
			<SideMenu
				itemClick={handleMenuClick}
				tabClick={() => {
					setInfoPanel(false);
				}}
				currentItem={mapItem}
				open={open}
				setOpen={setOpen}
			/>

			{/* The map which uses TransformWrapper/TransformComponent */}
			<MapView
				mapPosition={mapPosition}
				markerPosition={markerPos}
				mapZoom={mapZoom}
				mapItem={mapItem}
				imgW={image.w}
				imgH={image.h}
				setInfoPanel={(args) => {
					if (args === "setNull") {
						setMapItem(null);
						return;
					}

					setInfoPanel(!infoPanel);
					if (!infoPanel) {
						setOpen(true);
					}
				}}
				infoPanel={infoPanel}
				setItemStack={(args) => {
					// indicate that the last action was panning
					if (itemStack[itemStack.length - 1] !== "panned") {
						itemStack.push(args);
					}
				}}
			/>

			{/* Mist overlay for changing map destinations/page load effect */}
			<MistOverlay
				doAnimate={doAnimate}
				onAnimationEnd={() => {
					setDoAnimate(false);
				}}
				className={
					doAnimate ? (introAnimation === 1 ? "fade-intro" : "fade") : ""
				}
			>
				<Mist img="fog-high.png" />
				<Mist img="fog-low.png" />
				{introAnimation === 1 && (
					<Graphic duration={8}>
						{"“" + quotes[Math.floor(Math.random() * quotes.length)] + "”"}
						<br />
						<br />
						{"-Brandon Sanderson"}
					</Graphic>
				)}
				{mapItem && introAnimation > 1 && (
					<Graphic duration={2}>{mapItem.title}</Graphic>
				)}
			</MistOverlay>

			{
				<InfoPanel show={infoPanel}>
					<PopupInfo item={mapItem} show={infoPanel} />
					<CloseButton display={infoPanel}>
						<AiFillCloseCircle
							onClick={() => {
								setInfoPanel(false);
							}}
						/>
					</CloseButton>
				</InfoPanel>
			}
		</div>
	);
}

export default App;
