import styled from "styled-components";
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import MapTools from "./MapTools";
import MapMarker from "./MapMarker";

const Container = styled.img``;

const MapView = ({
	mapZoom,
	mapItem,
	mapPosition,
	imgW,
	imgH,
	infoPanel,
	setInfoPanel,
	markerPosition,
	setItemStack,
}) => {
	return (
		<TransformWrapper
			defaultScale={1}
			scale={mapZoom}
			options={{
				limitToBounds: false,
				limitToWrapper: true,
			}}
			positionX={-mapPosition.x}
			positionY={
				-mapPosition.y === 0
					? window.innerHeight / 2 - imgH / 2
					: -mapPosition.y
			}
			onPanning={(e) => {
				setItemStack("panned");
				// onPanningStart, remove the onclick-transition class (0.5s animation)
				//  so panning doesn't feel clunky
				const el = document.getElementsByClassName(
					"react-transform-element"
				)[0];
				el.classList.remove("onclick-transition");

				// add dragging class, so we can determine if the destination was clicked on for real,
				//  or if the destination was just clicked while panning the map
				el.classList.add("dragging");
			}}
			onPanningStop={() => {
				// remove dragging class
				const el = document.getElementsByClassName(
					"react-transform-element"
				)[0];
				el.classList.remove("dragging");
			}}
		>
			{({ zoomIn, zoomOut, resetTransform, ...rest }) => (
				<>
					<MapTools
						zoomIn={zoomIn}
						zoomOut={zoomOut}
						resetTransform={() => {
							resetTransform();
						}}
					/>

					<TransformComponent>
						<MapMarker
							x={markerPosition.x}
							y={markerPosition.y}
							mapItem={mapItem}
							setInfoPanel={(args) => {
								setInfoPanel(args);
							}}
							infoPanel={infoPanel}
						/>
						<Container
							src="./images/map.jpg"
							width={imgW}
							height={imgH}
						></Container>
					</TransformComponent>
				</>
			)}
		</TransformWrapper>
	);
};

export default MapView;
