import Spoilers from "./Spoilers";
import Destinations from "./Destinations";
import styled from "styled-components";
import Image from "./Image";

const Container = styled.div`
	position: relative;
	width: 100%;
	height: calc(100% - 80px);
	display: ${(props) => (props.display ? "flex" : "none")};
	flex-direction: column;

	@media only screen and (max-height: 432px) {
		height: calc(100% - 60px);
	}
`;
const Body = styled.div`
	position: relative;
	width: auto;
	height: auto;
	display: flex;
	flex-direction: column;
	margin: 5px;
	overflow-y: scroll;
	overflow-x: hidden;
`;
const Title = styled.p`
	position: relative;
	padding-top: 10px;
	margin: 0 auto;
	height: auto;
	font-size: 2em;
	font-family: "Charm", cursive;
	display: block;

	@media only screen and (max-height: 432px) {
		padding-top: 0px;
	}
`;
const Link = styled.a`
	padding: 2px 5px;
	margin: 10px auto;
	text-decoration: none;
	color: darkslateblue;
	background: none;
	transition: all 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);

	:hover {
		color: white;
		background: darkslateblue;
	}
`;
const Images = styled.div`
	width: 100%;
	height: auto;
	position: relative;
	text-align: center;
`;

const PopupInfo = ({ item, show }) => {
	return (
		<Container display={item ? (show ? true : false) : false}>
			<Title>{item && item.title}</Title>
			{item &&
				Destinations.map((map_item) => {
					if (map_item.id === item.id) {
						return (
							<Body>
								{map_item.description}
								<Link href={map_item.link} target="_blank">
									READ MORE
								</Link>
								<Images>
									<p>Click an image to enlarge</p>
									{map_item.images &&
										map_item.images.map((img, index) => {
											return (
												<>
													<Image
														path={img.path}
														credit={img.artist}
														show={show}
													/>
													{img.title === "Official" ? "" : img.title}
												</>
											);
										})}
								</Images>
							</Body>
						);
					}
				})}
		</Container>
	);
};

export default PopupInfo;
