import styled from "styled-components";
import { FaMapMarkerAlt, FaScroll, FaTimes } from "react-icons/fa";

const Marker = styled.div`
	position: absolute;
	width: auto;
	height: auto;
	color: #ff6161;
	font-size: 2em;
	border-radius: 20px;
	left: ${(props) => props.x - 1 + "px"};
	top: ${(props) => props.y + "px"};
	opacity: ${(props) => (props.display ? "0" : "1")};
	display: ${(props) => (props.display ? "none" : "block")};
	z-index: 10;
	animation: pulsate 4s cubic-bezier(0.19, 1, 0.22, 1) infinite;
	transition: all 0.3s;

	@keyframes pulsate {
		0% {
			transform: translate(-50%, -50%);
			color: rgba(59, 32, 32, 0.438);
		}
		50% {
			transform: translate(-50%, -60%);
			color: rgba(187, 41, 41, 0.856);
		}
		100% {
			transform: translate(-50%, -50%);
			color: rgba(59, 32, 32, 0.472);
		}
	}

	@media only screen and (max-device-width: 768px) {
		font-size: 1.4em;
	}
	@media only screen and (max-device-width: 432px) {
		font-size: 1em;
	}
`;

const ButtonContainer = styled.div`
	position: absolute;
	display: ${(props) => (props.display ? "none" : "flex")};
	justify-content: center;
	align-items: center;
	width: auto;
	height: auto;
	color: #272727;
	background: #af2626;
	border-radius: 5px;
	left: ${(props) => props.x + "px"};
	top: ${(props) => props.y + "px"};
	z-index: 10;
	transform: ${(props) =>
		props.translate ? "translate(-50%, 60%)" : "translate(-50%, -200%)"};
	font-family: "Charm", cursive;
	cursor: pointer;
	transition: all 0.8s cubic-bezier(0.19, 1, 0.22, 1);
	text-indent: 10px;
	opacity: ${(props) => (props.display ? "0" : "1")};
`;

const Button = styled.div`
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	width: 40px;
	height: 40px;
	margin: auto;

	@media only screen and (max-device-width: 768px) {
		font-size: 0.6em;
		width: 20px;
		height: 20px;
	}
	@media only screen and (max-device-width: 432px) {
		font-size: 0.6em;
		width: 20px;
		height: 20px;
	}
`;

const MapMarker = ({ x, y, mapItem, setInfoPanel, infoPanel }) => {
	// trDown is a boolean which indicates if the marker buttons should be positioned above the marker or below
	let trDown = false;

	// translate down if height is smaller than 960
	if (window.innerHeight < 768) {
		trDown = true;
	}
	// always translate down if the y-position of marker is near the top of the map
	if (y < 100) {
		trDown = true;
	}

	return (
		<div className="map-marker">
			<ButtonContainer
				x={x}
				y={y}
				display={mapItem === null}
				translate={trDown}
			>
				<Button
					onClick={() => {
						setInfoPanel();
					}}
					className="item-flag"
				>
					<FaScroll />
				</Button>
				<Button
					style={{
						color: "#af2626",
						background: "#1d1d1d",
						borderTopRightRadius: "5px",
						borderBottomRightRadius: "5px",
					}}
					onClick={() => {
						setInfoPanel("setNull");
					}}
					className="item-flag"
				>
					<FaTimes />
				</Button>
			</ButtonContainer>

			<Marker x={x} y={y} display={mapItem === null}>
				<FaMapMarkerAlt />
			</Marker>
		</div>
	);
};

export default MapMarker;
