import { useState } from "react";
import styled from "styled-components";
import { FaTimes } from "react-icons/fa";

const ImageDiv = styled.div`
	position: relative;
	background: url(${(props) => '"' + props.src + '"'});
	background-size: cover;
	background-position: center;
	width: 80%;
	height: 400px;
	min-height: 400px;
	margin: 10px auto;
	transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	cursor: pointer;
`;
const LargeImage = styled.div`
	position: relative;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: url(${(props) => '"' + props.src + '"'});
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
	touch-action: pan-x pan-y pinch-zoom;
	z-index: 2;
	transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);

	@media only screen and (max-width: 1024px) and (max-height: 768px) {
	}
`;
const Credits = styled.div`
	position: absolute;
	width: auto;
	height: auto;
	padding: 5px 10px;
	background: #000000;
	z-index: 4;
	color: #ffffff;
	font-style: italic;
	left: 100%;
	top: 0;
	transform: translateX(-100%);
	border-bottom-left-radius: 10px;
`;
const Container = styled.div`
	z-index: 50;
	position: fixed;
	display: ${(props) => (props.showing ? "flex" : "none")};
	flex-direction: column;
	justify-content: center;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
`;

const BlurBg = styled.div`
	position: absolute;
	z-index: 1;
	top: -5vh;
	left: -5vw;
	width: 110vw;
	height: 110vh;
	background: url(${(props) => '"' + props.src + '"'});
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	filter: blur(9px);
	transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-webkit-filter: blur(9px);
`;

const Button = styled.div`
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 150px;
	height: 70px;
	z-index: 10;
	left: 50%;
	top: calc(100% - 110px);
	transform: translateX(-50%);
	color: #ffffff;
	background: #e93030;
	font-size: 3.2em;
	cursor: pointer;
	transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	border-radius: 20px;
	border: 5px solid transparent;

	:hover {
		color: #ff1818;
		background: rgba(173, 33, 33, 0.767);
		border: 5px solid #ad2121;
	}

	@media only screen and (max-width: 768px) {
		font-size: 2.8em;
		width: 110px;
		height: 60px;
		top: calc(100% - 90px);
	}
`;

const Image = ({ path, credit, show }) => {
	const [showing, setShowing] = useState(false);

	return (
		<>
			<ImageDiv
				src={"./images/destinations/" + path}
				onClick={() => {
					setShowing(true);
				}}
			></ImageDiv>
			<Container showing={showing}>
				{show && <LargeImage src={"./images/destinations/" + path} />}
				{credit && <Credits>{credit}</Credits>}
				<BlurBg src={"./images/destinations/" + path} />
				<Button
					onClick={() => {
						setShowing(false);
					}}
				>
					<FaTimes />
				</Button>
			</Container>
		</>
	);
};

export default Image;
